// <script>
import sendAjax from '../../js/common/request.js';
export default {
	ready() {
		this.getPersonal();
	},
	data () {
		return  {
			isData: false,
			data: {
			},
			tab: 1,
			isShowOne: false,
			openList: [true, true, true, true, true, true, true, true ]
		}
	},
	watch: {
		'isShowOne' (newVal, oldVal) {
			let list = [];
			if (newVal) {
				list[0] = true;
				for (let i = 1; i < this.openList.length; i++) {
					list.push(false);
				}
			} else {
				for (let i = 0; i < this.openList.length; i++) {
					list.push(true);
				}
			}
			this.openList = list;
		}
	},
	methods: {
		openTable (index) {
			let list = [];
			if (this.isShowOne) {
				for (let i = 0; i < this.openList.length; i++) {
					list.push(false);
				}
				list[index] = true;
			} else  {
				for (let i = 0; i < this.openList.length; i++) {
					list.push(this.openList[i]);
				}
				list[index] = !list[index];
			}
			this.openList = list;
		},
		getPersonal () {
			let _this = this;
			let id = this.$route.query.pid || '';
			sendAjax({
				url: '/finc/perform/daily',
				data: {
					user_id: id,
				},
				type: 'GET',
				success (result) {
					switch (result.code) {
						case 0:
							_this.isData = true;
							_this.data = result.data;
							break;
					}
				}
			});
		}
	}
}
